<template>
  <div id="image-crop">
    <!--영정사진crop pop-->
    <div class="photocrop active">
      <div class="photocrop-pop__inner">
        <h1>사진 편집하기</h1>
        <p>
          원하는 부분이 나올 수 있도록 틀(캔버스)을 <br />조절하신 후
          '사진등록'을 클릭해 주세요.
        </p>

        <div class="cropspace">
          <cropper
            v-if="flagNewDisplay === 1"
            class="cropper"
            :src="img"
            :stencil-props="{
              aspectRatio: 11 / 14
            }"
            @change="change"
          />
          <cropper
            v-if="flagNewDisplay === 0"
            class="cropper"
            :src="img"
            :stencil-props="{
              aspectRatio: 3 / 4
            }"
            @change="change"
          />
        </div>
      </div>
      <button class="btn_photocrop" @click="regist">사진 등록</button>
      <button class="btn_service_colse" @click="close()">닫기</button>
    </div>
  </div>
</template>

<script>
import { Cropper } from "vue-advanced-cropper";
import File from "@tanker/file-ponyfill";
export default {
  components: {
    Cropper
  },
  props: ["files", "methods", "close"],

  data() {
    return {
      flagNewDisplay: 1,
      img: "",
      canvas: null,
      returnFiles: []
    };
  },
  mounted() {
    debugger;
    this.flagNewDisplay = parseInt(this.$store.getters.getObituaryDisplayType);
    this.img = URL.createObjectURL(this.files[0]);
  },
  methods: {
    blobToFile(theBlob, fileName) {
      //A Blob() is almost a File() - it's just missing the two properties below which we will add
      debugger;
      theBlob.lastModifiedDate = new Date();
      theBlob.name = fileName;

      const fileObject = new CustomFile([theBlob], fileName);

      return fileObject;
    },

    change({ coordinates, canvas }) {
      console.log(coordinates, canvas);
      this.canvas = canvas;
    },
    regist() {
      debugger;
      this.canvas.toDataURL();
      const context = this;

      if (this.canvas.msToBlob) {
        // IE
        const blob = this.canvas.msToBlob();
        context.returnFiles.push(
          new File([blob], context.files[0].name, {
            type: "image/jpeg",
            lastModified: new Date()
          })
        );
        //context.returnFiles.push(context.blobToFile(blob,context.files[0].name))
        context.methods(context.returnFiles, context.flagNewDisplay);
        return;
      }
      this.canvas.toBlob(blob => {
        try {
          context.returnFiles = [];
          console.log(blob);
          context.returnFiles.push(
            new File([blob], context.files[0].name, {
              type: "image/jpeg",
              lastModified: new Date()
            })
          );
          context.methods(context.returnFiles, this.flagNewDisplay);
          this.$store.commit("OBITUARY_DISPLAY_TYPE", this.flagNewDisplay);
        } catch (err) {
          // Workaround for IE 11
          debugger;
          const blob = this.documentService.base64ToBlob(
            this.document.documentBlob.documentData,
            this.document.documentDataFormat
          );
          const file = this.documentService.blobToFile(
            blob,
            this.document.documentName
          );
          context.returnFiles.push(file);

          context.methods(context.returnFiles, this.flagNewDisplay);
          this.$store.commit("OBITUARY_DISPLAY_TYPE", this.flagNewDisplay);
        }
      });
    }
  }
};
</script>

