<template>
  <!--장지 pop-->
  <div class="jangji_pop active">
    <div class="jangji_pop_inner">
      <header>
        <h4>음악선택</h4>
      </header>
      <template v-for="(music, index) in getMusicList">
        <div
          v-if="music.musicFile.length"
          :key="index"
          class="preview-box mt-6"
        >
          <div class="member-info">
            <img class="mr-2" src="@/assets/icons/obituary/volume.svg" alt="" />
            {{ music.musicFile[0].fileName }}
            <!-- <h4>{{ musicName(music.musicFile) }}</h4> -->
          </div>
          <button @click="selectMusic(music)">선택</button>
        </div>
      </template>

      <div class="button__wraps">
        <button class="cancel" @click="close">닫기</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  props: ["methods"],
  computed: {
    ...mapGetters(["getMusicList"])
  },
  data() {
    return {
      music: {
        id: null,
        musicMember: null
      },
      files: []
    };
  },
  methods: {
    musicName(file) {
      debugger;
      return file[0].fileName;
    },
    changeFiles(event) {
      if (event.target.files.length > 0) {
        this.files = event.target.files;
      }
    },
    fileUpload() {
      this.$refs.fileupload.click();
    },
    close() {
      this.methods();
    },
    selectMusic(music) {
      this.methods(music);
    },
    init() {
      this.music = {
        id: null,
        musicMember: null
      };
      this.files = [];
    },
    addMusic() {
      const parameter = {
        music: this.music,
        files: this.files
      };
      this.$store.dispatch("REQUEST_REGIST_MUSIC", parameter);
      this.init();
    },
    deleteMusic(id) {
      const parameter = {
        id: id
      };
      this.$store.dispatch("REQUEST_DELETE_MUSIC", parameter);
    }
  }
};
</script>

