    //let recaptchaScript = document.createElement('script')
    //recaptchaScript.setAttribute('src', '@/assets/js/ux_common.js')
    //document.head.appendChild(recaptchaScript)
  },
  beforeDestroy() {
    this.$store.commit("OBITUARY_INIT");
  },
  methods: {
    routingBack() {
      this.$router.go(-1);
    },
    firstOrder() {
      this.obituaryRegOrder = "first";
    },
    secondOrder() {
      this.obituaryRegOrder = "second";
    },
    thirdOrder() {
      this.obituaryRegOrder = "third";
    },
    fourthOrder() {
      this.obituaryRegOrder = "fourth";
    },
    fifthOrder() {
      this.obituaryRegOrder = "fifth";
    },

    changeDate() {
      if (
        this.endDateString !== null &&
        this.endDateString !== undefined &&
        this.endDateString !== ""
      ) {
        return;
      }
      let obituaryDieDate = new Date(this.obituary.obituaryDieDate);

      obituaryDieDate.setDate(obituaryDieDate.getDate() + 2);
      //this.endDateString = this.YYMMDDSLASHDate(obituaryDieDate)
    },
    accountCertifyInit(id, index) {
      this.obituaryMemberList[id].members[index].success = false;
    },

    accountCertify(obituaryMember) {
      const parameter = {
        accountNumber: obituaryMember.obituaryMemberBankNumber,
        bankCode: obituaryMember.obituaryMemberBank
      };
      this.$store.dispatch("REQUEST_ACCOUNT_CERTIFY", parameter).then(data => {
        if (data.result) {
          if (obituaryMember.obituaryMemberName.indexOf(data.name) > -1) {
            alert("계좌가 확인되었습니다.");
            obituaryMember.success = true;
          } else {
            obituaryMember.success = true;
            obituaryMember.bankEqual = false;
            obituaryMember.bankHolder = data.name;
            this.modal = true;
          }
        }
      });
    },
    openJangJi(grave) {
      this.jangjiFlag = !this.jangjiFlag;
      if (this.jangjiFlag) {
        this.$store.commit("START_DEEMED");
      } else {
        if (grave !== null && grave !== undefined) {
          this.obituary.obituaryPlace = grave;
          this.placeChange();
        }
        this.$store.commit("END_DEEMED");
      }
    },
    openMusic(music) {
      this.musicFlag = !this.musicFlag;
      if (this.musicFlag) {
        this.$store.commit("START_DEEMED");
      } else {
        if (music !== null && music !== undefined) {
          this.obituary.obituaryMusic = { id: music.id };
          this.musicName = music.musicFile[0].fileName;
        }
        this.$store.commit("END_DEEMED");
      }
    },

    onlyNumberAge() {
      this.obituary.obituaryAge = this.obituary.obituaryAge.replace(
        /[^0-9.]/g,
        ""
      );
    },
    onlyNumberPhone(id, memberIndex) {
      this.obituaryMemberList[id].members[
        memberIndex
      ].obituaryMemberPhone = this.obituaryMemberList[id].members[
        memberIndex
      ].obituaryMemberPhone.replace(/[^0-9.]/g, "");
    },
    inputCasketTime($event) {
      this.casketTimeString = this.changeHHMM($event.target.value);
    },
    inputReligionTime($event) {
      this.religionTimeString = this.changeHHMM($event.target.value);
    },
    placeCheck(event) {
      this.placeVisibleCheck = event.target.checked;
      if (this.placeVisibleCheck) {
        this.obituary.obituaryPlace = "";
      }
    },
    placeChange() {
      if (this.obituary.obituaryPlace !== "") {
        this.placeVisibleCheck = false;
      }
    },
    endTimeCheck(event) {
      this.endTimeVisibleCheck = event.target.checked;
      if (this.endTimeVisibleCheck) {
        this.endDateString = "";
        this.endTimeString1 = null;
        this.endTimeString2 = null;
      }
    },
    endCasketCheck(event) {
      this.endCasketVisibleCheck = event.target.checked;
      if (this.endCasketVisibleCheck) {
        this.casketDateString = "";
        this.casketTimeString1 = null;
        this.casketTimeString2 = null;
      }
    },

    endReligionCheck(event) {
      this.endReligionVisibleCheck = event.target.checked;
      if (this.endReligionVisibleCheck) {
        this.religionDateString = "";
        this.religionTimeString1 = null;
        this.religionTimeString2 = null;
      }
    },
    endDateChange($event) {
      if (
        (this.endDateString !== null && this.endDateString !== "") ||
        (this.endTimeString1 !== null && this.endTimeString1 !== "") ||
        (this.endTimeString2 !== null && this.endTimeString2 !== "")
      ) {
        this.endTimeVisibleCheck = false;
      } else {
        this.endTimeVisibleCheck = true;
      }
    },
    endTimeChange($event) {
      if (this.endTimeVisibleCheck) {
        if (this.endDateString === "" || this.endTimeString === "") {
          this.endTimeVisibleCheck = false;
        }
      }
      this.endTimeString = this.changeHHMM($event.target.value);
    },

    addObituaryMember() {
      if (this.obituaryMemberList.length === 0) {
        this.obituaryMemberList.push({
          id: null,
          obituaryMemberObituary: {
            id: null
          },
          obituaryMemberName: "",
          obituaryMemberType: "",
          obituaryMemberPhone: "",
          obituaryMemberLeader: false,
          obituaryMemberBank: "",
          obituaryMemberBankNumber: ""
        });
      } else {
        this.obituaryMemberList.push({
          id: null,
          obituaryMemberObituary: {
            id: null
          },
          obituaryMemberName: "",
          obituaryMemberType: "",
          obituaryMemberPhone: "",
          obituaryMemberLeader: false,
          obituaryMemberBank: "",
          obituaryMemberBankNumber: ""
        });
      }
    },
    deleteObituaryMember(index) {
      this.obituaryMemberList.splice(index, 1);
    },

    obituaryReg() {
      if (
        this.enterTimeString1 != null &&
        this.enterTimeString1 !== "" &&
        this.enterTimeString2 !== null &&
        this.enterTimeString2 !== ""
      ) {
        this.enterTimeString =
          this.enterTimeString1 + ":" + this.enterTimeString2;
      }
      if (
        this.casketTimeString1 != null &&
        this.casketTimeString1 !== "" &&
        this.casketTimeString2 !== null &&
        this.casketTimeString2 !== ""
      ) {
        this.casketTimeString =
          this.casketTimeString1 + ":" + this.casketTimeString2;
      }
      if (
        this.religionTimeString1 != null &&
        this.religionTimeString1 !== "" &&
        this.religionTimeString2 !== null &&
        this.religionTimeString2 !== ""
      ) {
        this.religionTimeString =
          this.religionTimeString1 + ":" + this.religionTimeString2;
      }
      if (
        this.endTimeString1 != null &&
        this.endTimeString1 !== "" &&
        this.endTimeString2 !== null &&
        this.endTimeString2 !== ""
      ) {
        this.endTimeString = this.endTimeString1 + ":" + this.endTimeString2;
      }

      if (!this.validationCheck()) {
        return;
      }

      let obituaryCopy = JSON.parse(JSON.stringify(this.obituary));

      if (
        obituaryCopy.obituaryAge === null ||
        obituaryCopy.obituaryAge === ""
      ) {
        obituaryCopy.obituaryAge = null;
        obituaryCopy.obituaryAgeYn = true;
      }
      if (
        obituaryCopy.obituarySex === null ||
        obituaryCopy.obituarySex === ""
      ) {
        obituaryCopy.obituarySexYn = true;
        obituaryCopy.obituarySex = null;
      }

      if (
        obituaryCopy.obituaryDieDate !== null &&
        obituaryCopy.obituaryDieDate !== undefined &&
        obituaryCopy.obituaryDieDate !== ""
      ) {
        obituaryCopy.obituaryDieDate =
          obituaryCopy.obituaryDieDate + " 00:00:00";
      }
      if (
        this.enterDateString !== null &&
        this.enterDateString !== "" &&
        this.enterTimeString !== null &&
        this.enterTimeString !== ""
      ) {
        obituaryCopy.obituaryEnterDate =
          this.enterDateString + " " + this.enterTimeString + ":00";
      } else {
        obituaryCopy.obituaryEnterDate = null;
      }

      if (
        this.casketDateString !== null &&
        this.casketDateString !== "" &&
        this.casketTimeString !== null &&
        this.casketTimeString !== ""
      ) {
        obituaryCopy.obituaryCasketDate =
          this.casketDateString + " " + this.casketTimeString + ":00";
      } else {
        obituaryCopy.obituaryCasketDate = null;
      }

      if (
        this.religionDateString !== null &&
        this.religionDateString !== "" &&
        this.religionTimeString !== null &&
        this.religionTimeString !== ""
      ) {
        obituaryCopy.obituaryReligionDate =
          this.religionDateString + " " + this.religionTimeString + ":00";
      } else {
        obituaryCopy.obituaryReligionDate = null;
      }

      if (
        this.endDateString !== null &&
        this.endDateString !== "" &&
        this.endTimeString !== null &&
        this.endTimeString !== ""
      ) {
        obituaryCopy.obituaryEndDate =
          this.endDateString + " " + this.endTimeString + ":00";
      } else {
        obituaryCopy.obituaryEndDate = null;
      }

      obituaryCopy.obituaryObituaryMember = this.obituaryMemberList
        .map(v => v.members)
        .flat();

      if (obituaryCopy.obituaryEnterRoom.id === "") {
        obituaryCopy.obituaryEnterRoom = null;
      }
      if (this.memberInfo.memberAttr === "TYPE-1") {
        obituaryCopy.obituaryCorp = { codeId: this.memberInfo.memberCorpCode };
      }
      this.$store.commit("START_DEEMED");
      this.agreeFlag = true;

      this.comfirmobituaryCopy = obituaryCopy;
      this.comfirmfile = this.files;
    },

    submit(val) {
      if (!val) {
        alert("개인정보 동의를 진행해 주세요.");
        return;
      }
      this.agreeFlag = false;
      this.$store.commit("END_DEEMED");
      const parameter = {
        obituary: this.comfirmobituaryCopy,
        files: this.comfirmfile
      };
      debugger;

      this.$store.dispatch("REQUEST_OBITUARY_REG", parameter);
      debugger;
    },

    async changeFiles(event) {
      const context = this;
      if (event.target.files.length > 0) {
        debugger;
        //this.imageResize(event.target.files[0])

        let file = event.target.files[0]; // 입력받은 file객체

        // 이미지 resize 옵션 설정 (최대 width을 100px로 지정)
        const options = {
          maxSizeMB: 0.2,
          maxWidthOrHeight: 1300
        };

        try {
          debugger;
          const compressedFile = await imageCompression(file, options);
          //setFile(compressedFile);

          const fileParam = [];
          fileParam.push(compressedFile);
          context.canvasFlag = true;

          context.selectedFiles = fileParam;
        } catch (error) {
          console.log(error);
        }
      }
    },
    cropedClose() {
      this.canvasFlag = false;
    },
    cropedFiles(files, displayType) {
      debugger;
      this.files = files;
      this.url = URL.createObjectURL(this.files[0]);
      this.obituary.obituaryDisplayType = displayType;
      this.canvasFlag = false;
    },
    reFileCrop() {
      if (this.selectedFiles.length > 0 && this.files.length > 0) {
        if (
          this.selectedFiles === null ||
          this.selectedFiles === undefined ||
          this.selectedFiles.length === 0
        ) {
          this.selectedFiles = this.files;
          this.canvasFlag = true;
        } else {
          this.canvasFlag = true;
        }
      }
    },
    fileUpload() {
      this.$refs.fileupload.click();
    },

    validationCheck() {
      if (
        this.obituary.obituaryNickName === null ||
        this.obituary.obituaryNickName === ""
      ) {
        this.obituary.obituaryNickName = this.obituary.obituaryName;
      }
      if (
        this.obituary.obituaryName === null ||
        this.obituary.obituaryName === ""
      ) {
        alert("이름을 입력해 주세요.");
        this.firstOrder();
        return false;
      }
      /*
			if(this.obituary.obituaryAge===null || this.obituary.obituaryAge === ''){
				alert('나이를 입력해 주세요.')
				return false
			}
			*/
      if (
        this.obituary.obituarySex === null ||
        this.obituary.obituarySex === ""
      ) {
        alert("성별을 선택해 주세요.");
        this.firstOrder();
        return false;
      }

      if (this.mortuaryInFlag === "in") {
        if (this.memberInfo.memberAttr === "TYPE-0") {
          if (
            this.obituary.obituaryMortuary.id === null ||
            this.obituary.obituaryMortuary.id === undefined ||
            this.obituary.obituaryMortuary.id === ""
          ) {
            alert("빈소를 선택해 주세요.");
            this.firstOrder();
            return false;
          }
        } else {
          if (
            this.obituary.obituaryMortuaryName === null ||
            this.obituary.obituaryMortuaryName === undefined ||
            this.obituary.obituaryMortuaryName === ""
          ) {
            alert("빈소를 입력해 주세요.");
            this.firstOrder();
            return false;
          }
        }
      }
      if (
        this.enterDateString !== null &&
        this.enterDateString !== undefined &&
        this.enterDateString !== ""
      ) {
        if (this.enterTimeString1 === null || this.enterTimeString1 === "") {
          alert("입실시간을 입력해주세요 ");
          this.firstOrder();
          return;
        }

        if (this.enterTimeString2 === null || this.enterTimeString2 === "") {
          alert("입실시간을 입력해주세요 ");
          this.firstOrder();
          return;
        }
      }

      if (
        this.casketDateString !== null &&
        this.casketDateString !== undefined &&
        this.casketDateString !== ""
      ) {
        if (this.casketTimeString1 === null || this.casketTimeString1 === "") {
          alert("입관시간을 입력해주세요 ");
          this.firstOrder();
          return;
        }

        if (this.casketTimeString2 === null || this.casketTimeString2 === "") {
          alert("입관시간을 입력해주세요 ");
          this.firstOrder();
          return;
        }
      }

      if (
        this.religionDateString !== null &&
        this.religionDateString !== undefined &&
        this.religionDateString !== ""
      ) {
        if (
          this.religionTimeString1 === null ||
          this.religionTimeString1 === ""
        ) {
          alert("종교 행사시간을 입력해주세요 ");
          this.firstOrder();
          return;
        }

        if (
          this.religionTimeString2 === null ||
          this.religionTimeString2 === ""
        ) {
          alert("종교 행사시간을 입력해주세요 ");
          this.firstOrder();
          return;
        }
      }
      if (this.memberInfo.memberAttr === "TYPE-1") {
        if (
          this.obituary.obituaryAddrName === null ||
          this.obituary.obituaryAddrName === undefined ||
          this.obituary.obituaryAddrName === ""
        ) {
          alert("장례식장 명을 입력해 주세요.");
          this.firstOrder();
          return false;
        }
        if (
          this.obituary.obituaryAddr === null ||
          this.obituary.obituaryAddr === undefined ||
          this.obituary.obituaryAddr === ""
        ) {
          alert("장례식장 주소을 입력해 주세요.");
          return false;
        }
      }
      if (this.obituary.obituaryMusicYn === "true") {
        if (
          this.obituary.obituaryMusic === null ||
          this.obituary.obituaryMusic.id === null ||
          this.obituary.obituaryMusic.id === undefined ||
          this.obituary.obituaryMusic.id === ""
        ) {
          alert("음악을 선택해 주세요.");
          this.firstOrder();
          return false;
        }
      } else {
        this.obituary.obituaryMusic = null;
      }

      debugger;

      // if(this.obituary.obituaryDieDate===null || this.obituary.obituaryDieDate === ''){
      // 	alert('별세일을 입력해 주세요.')
      // 	return false
      // }

      if (
        !this.endTimeVisibleCheck &&
        (this.endDateString === undefined ||
          this.endDateString === null ||
          this.endDateString === "" ||
          this.endTimeString1 === undefined ||
          this.endTimeString1 === null ||
          this.endTimeString1 === "" ||
          this.endTimeString2 === undefined ||
          this.endTimeString2 === null ||
          this.endTimeString2 === "")
      ) {
        alert("발인일을 입력해 주세요.");
        this.firstOrder();
        return false;
      }

      if (!this.placeVisibleCheck && this.obituary.obituaryPlace === "") {
        alert("장지를 입력해 주세요.");
        this.firstOrder();
        return false;
      }

      let memberValid = true;

      this.obituaryMemberList.forEach(v => {
        v.members.forEach(arg => {
          if (!arg.obituaryMemberType) {
            memberValid = false;
            alert("고인과의 관계를 입력해 주세요.");
            debugger;
          }
        });
      });

      if (!memberValid) return;

      this.obituaryMemberList.forEach(v => {
        v.members.forEach(arg => {
          if (!arg.obituaryMemberName) {
            memberValid = false;
            alert("상주명을 입력해 주세요.");
          }
        });
      });

      if (!memberValid) return;

      this.obituaryMemberList.forEach(v => {
        v.members.forEach(arg => {
          if (arg.obituaryMemberLeader) {
            if (!arg.obituaryMemberPhone) {
              memberValid = false;
              alert(
                arg.obituaryMemberName + "대표상주의 전화번호를 입력해 주세요."
              );
            }
          }
        });
      });

      if (!memberValid) return;

      if (this.moneyOnOff) {
        this.obituaryMemberList.forEach(v => {
          v.members.forEach(arg => {
            if (
              arg.account === true &&
              (arg.obituaryMemberBank !== "" ||
                arg.obituaryMemberBankNumber !== "")
            ) {
              if (arg.success === false) {
                memberValid = false;
                alert(arg.obituaryMemberName + "의 계좌 인증을 진행해 주세요.");
              }
            }
          });
        });
      }

      if (!memberValid) return;

      return true;
    },
    mortuarySelectTypeChange() {
      if (this.memberInfo.memberAttr === "TYPE-0") {
        if (this.mortuaryInFlag === "in") {
          this.obituary.obituaryMortuary = { id: "" };
        } else {
          this.obituary.obituaryMortuary = { id: "" };
        }
      } else {
        if (this.mortuaryInFlag === "in") {
          this.obituary.obituaryMortuaryName = "";
        } else {
          this.obituary.obituaryMortuaryName = null;
        }
      }
    },
    addrRegPopup() {
      $("#address-search").addClass("active");
      this.$store.commit("START_DEEMED");
    },
    registAddress(val) {
      this.obituary.obituaryAddrName = val.place;
      this.obituary.obituaryAddr = val.address;
      this.obituary.obituaryNumber = val.phone.replace(/-/gi, "");
      this.obituary.post = val.post;

      this.obituary.obituaryAddrX = val.x;
      this.obituary.obituaryAddrY = val.y;
      this.obituary.obituaryFuneralAddr = val.memberFuneralAddr;
    },

    addMemberType() {
      debugger;
      this.obituaryMemberList.push({
        type: "",
        members: []
      });

      debugger;
    },
    deleteMember(id, name) {
      const memberList = this.obituaryMemberList[id];
      const findIdx = memberList.members.findIndex(
        v => v.obituaryMemberName === name
      );
      memberList.members.splice(findIdx, 1);
    },
    addMember(id) {
      if (!this.addMemeberValid) {
        debugger;
        return;
      }

      this.addMemeberValid = false;

      const name = this.$refs["temp" + id][0].$refs.input.value;
      debugger;
      if (
        this.obituaryMemberList[id].members.find(
          v => v.obituaryMemberName === name
        )
      ) {
        alert("같은 이름의 상주가 이미 등록되어져 있습니다");
        this.addMemeberValid = true;
        return;
      }

      if (name === "" || name.length < 2) {
        return;
      }

      this.obituaryMemberList[id].members.push({
        obituaryMemberName: name,
        obituaryMemberType: this.obituaryMemberList[id].type,
        account: false,
        showAccount: false,
        success: false,
        obituaryMemberLeader: false,
        obituaryMemberBank: null,
        obituaryMemberBankNumber: null,
        bankEqual: null,
        bankHolder: null,
        obituaryMemberPhone: null
      });

      this.$refs["temp" + id][0].reset();
      this.$refs["temp" + id][0].focus();

      debugger;
      this.addMemeberValid = true;
    },

    addMembers() {
      if (this.obituaryMemberList.map(v => v.members).flat().length <= 0) {
        return alert("입력된 상주가 없습니다");
      }

      // 상주 전체 등록시 가장 처음에 있는 사람 대표상주로
      const firstMember = this.obituaryMemberList.map(v => v.members).flat()[0];
      firstMember.obituaryMemberLeader = true;
      firstMember.account = true;
      firstMember.showAccount = true;

      this.thirdOrder();
    },

    checkAccountService(id, memberIdx) {
      const member = this.obituaryMemberList[id].members[memberIdx];

      if (!member) {
        return;
      }

      member.account = !member.account;

      if (member.account === true) {
        member.showAccount = true;
      }
    },
    addAccount(id, index) {
      this.obituaryMemberList[id].members[index].success = true;
      this.obituaryMemberList[id].members[index].bankEqual = true;
      this.modal = false;
    },
    addAccounts() {
      if (this.obituaryMemberList.map(v => v.members).flat().length <= 0) {
        return alert("입력된 상주가 없습니다");
      }

      if (!this.moneyOnOff) {
        this.tempList = JSON.parse(JSON.stringify(this.obituaryMemberList));
        this.fourthOrder();
        return;
      }

      const isAllSuccess = this.obituaryMemberList
        .map(v => v.members)
        .flat()
        .find(arg => arg.account && arg.success === false);

      if (isAllSuccess) {
        return alert("인증되지 않은 계좌가 있습니다");
      }

      this.tempList = JSON.parse(JSON.stringify(this.obituaryMemberList));
      this.fourthOrder();
    },

    // 순서 변경
    resetOrder() {
      this.tempList = null;
      this.tempList = JSON.parse(JSON.stringify(this.obituaryMemberList));
    },

    changeArrayOrder(list, targetIdx, moveValue) {
      if (list.length < 0) return;

      const newPosition = targetIdx + moveValue;

      if (newPosition < 0 || newPosition >= list.length) return;

      const target = list.splice(targetIdx, 1)[0];

      list.splice(newPosition, 0, target);
    },
    completeChangeOrder() {
      if (this.obituaryMemberList.map(v => v.members).flat().length <= 0) {
        return alert("입력된 상주가 없습니다");
      }
      if (this.tempList === null) {
        return this.fourthOrder();
      }

      this.obituaryMemberList = this.tempList;
      this.fourthOrder();
    },
    changeLeader(id, memberIdx) {
      const member = this.obituaryMemberList[id].members[memberIdx];

      const preLeader = this.obituaryMemberList
        .map(v => v.members)
        .flat()
        .filter(args => args.obituaryMemberLeader === true);

      if (preLeader.length < 1) {
        member.obituaryMemberLeader = true;
        return;
      }

      if (JSON.stringify(preLeader[0]) !== JSON.stringify(member)) {
        preLeader[0].obituaryMemberLeader = false;
        member.obituaryMemberLeader = true;
      }
    },
    imagePopupClose() {
      this.canvasFlag = false;
    },

    replaceBackCodeToName(val) {
      switch (val) {
        case "0002":
          return "산업은행";
        case "0003":
          return "기업은행";
        case "0004":
          return "국민은행";
        case "0007":
          return "수협중앙회";
        case "0011":
          return "농협은행";
        case "0020":
          return "우리은행";
        case "0023":
          return "SC제일은행";
        case "0027":
          return "한국씨티은행";
        case "0031":
          return "대구은행";
        case "0032":
          return "부산은행";
        case "0034":
          return "광주은행";
        case "0035":
          return "제주은행";
        case "0037":
          return "전북은행";
        case "0039":
          return "경남은행";
        case "0045":
          return "새마을금고연합회";
        case "0048":
          return "신협";
        case "0050":
          return "저축은행";
        case "0064":
          return "산림조합";
        case "0071":
          return "우체국";
        case "0081":
          return "하나은행";
        case "0088":
          return "신한은행";
        case "0089":
          return "케이뱅크";
        case "0090":
          return "카카오뱅크";

        default:
          break;
      }
    }
  }
};
</script>
